  margin-top: 0; }

.parsley-errors-list {
  position: relative;
  display: none;
  padding-left: 21px; }
  .parsley-errors-list.filled {
    display: block;
    margin: 0;
    margin-top: 9px; }
    .parsley-errors-list.filled:before {
      content: "\f534";
      display: inline-block;
      -webkit-font-smoothing: antialiased;
      font: normal 16px/1 'dashicons';
      vertical-align: top;
      position: absolute;
      top: 0;
      left: 0;
      color: #b4b9be; }
    .parsley-errors-list.filled li + li {
      margin: 0; }
    .parsley-errors-list.filled li {
      margin: 0; }

.assely-button {
  padding: 12px;
