  text-align: center;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  display: inline-block; }
  .assely-button:hover {
    background-color: #00a0d2; }
  .assely-button.block {
    display: block; }
  .assely-button.sm {
    padding: 4px 3px 1px 3px; }
    .assely-button.sm i {
      font-size: 18px; }
  .assely-button.neutral {
    background-color: #b4b9be; }
    .assely-button.neutral:hover {
      background-color: #a0a5aa; }
  .assely-button-spacer {
    width: 1px;
