  border-bottom: 2px solid #ddd;
  background-color: #fff;
  border-radius: 3px;
  transition: border-color 700ms ease; }
  .assely-box.is-invalid {
    border-top: 6px solid #FF5252 !important; }
  .assely-box__header {
    position: relative;
    border-bottom: 1px solid #ddd;
